$break-small: 500px;
$break-medium: 800px;
.rml-btn {

  $slideDownSize: 100px;
  .rml-deeplink---icon {

  }

  .rml-deeplink--hint {
    visibility: hidden;
    &.rml-hint__show {
      visibility: visible;
      //display: block;
      transform: translate3d(0, 0, 0);
      margin-bottom: 0px;
      opacity: 1;
      transition: transform ease-in-out 300ms, margin-bottom ease-in-out 300ms, opacity ease-in-out 200ms 100ms;
      z-index: auto;
    }
    transform: translate3d(0, -$hintSlideDownSize, 0);
    margin-bottom: -$hintSlideDownSize;
    opacity: 0;

    //display: none;
    transition: transform ease-in-out 300ms, margin-bottom ease-in-out 300ms, opacity ease-in-out 150ms;
    z-index: -1;
  }

  .rml-deeplink--hint-link {
    display: block;
    color: $red;
  }

  .rml-deeplink--heading {
    font-size: 18px;
  }

  .rml-deeplink--row, .rml-custom-content {
    max-width: 320px;
    margin: 0 auto;
  }

  .rml-deeplink--row {
    &:focus {
      text-decoration: none;
    }
    &:hover {
      color: #fefefe;
      text-decoration: none;
    }

    color: white;
    align-items: center;
    border-bottom: 1px solid $red;
    margin-top: 50px;
    margin-bottom: 35px;
    cursor: pointer;
    display: flex;
    flex-wrap: wrap;

  }

  .rml-deeplink--text {

  }

  .rml-deeplink--icon {
    flex-grow: 1;
    text-align: right;
    svg {
      fill: #fff;
      width: 60px;
      height: 60px;
    }
  }
}

.rml-deeplink--link {
  display: block;
  margin-top: 10px;
  margin-bottom: 15px;
  input[type="text"] {
    background: transparent;
    border: none;
    cursor: pointer;
    color: $red;
    padding: 0;
  }
  input[type="text"]:focus {
    outline: none;
  }

}

.rml-deeplink--links {
}

.rml-deeplink--slider {
  width: 200%;
  transition: all 250ms ease-in-out;

  &:after {
    clear: left;
  }
}

.rml-deeplink--slide {
  width: 50%;
  float: left;
}

.rml-generated-content {
  overflow: hidden;
}

.rml-deeplink__hint {
  transform: translate(-50%, 0);
  & > :first-child {
    opacity: 0;
    visibility: hidden;

  }
  .rml-deeplink--row {
    max-width: 100%;
  }
  .rml-deeplink--slide.rml-deeplink--alternate {
    /*padding-left: 195px;
    background-image: url(https://a.storyblok.com/f/cf57e7bb5c/iphone.png);
    background-repeat:no-repeat;
    height: 500px;
    background-size: 150px;
    background-position: 0 50px;*/
  }
}

.rml-deeplink__hint-container {
  display: flex;
  @media screen and (max-width: $break-small) {
    flex-flow: column-reverse;
  }
  .rml-deeplink--row {
    border: none;

  }
}

.rml-icons__copy {
  width: 1.4rem;
  height: 1.4rem;
  fill: $red;
}

.rml-deeplink__hint-container {
  .rml-deeplink__hint-img {
    width: 75%;
    margin-top: 50px;
    padding-right: 5%;
    text-align: center;
    @media screen and (min-width: $break-small+1px) and (max-width: $break-medium) {
      width: 100%;

    }
    img {
      max-width: 100%;
      max-height: none;
    }
    @media screen and (max-width: $break-small) {
      margin-top: 0;
      margin: 0 auto;

      img {
        max-height: 165px;
        width: auto;
      }
    }
  }
}

